<template>
  <div>
    <!-- 头部 -->
    <nav-bar class="home-nav">
      <div slot="center" class="header">
        <img src="@/styles/img/4、微网站搜索_03.png" alt />
      </div>
    </nav-bar>
    <!-- 我的订单 -->
    <div class="oeder">
      <i class="iconfont icon-fanhuijian"></i>
      <h3>我的订单</h3>
    </div>

    <div class="bar">
      <a class="word" :class="{word4:active==='1'}" @click="handleTabClick('1')">全部</a>
      <a class="word1" :class="{word4:active==='2'}" @click="handleTabClick('2')">待付款</a>
      <a class="word2" :class="{word4:active==='3'}" @click="handleTabClick('3')">待发货</a>
      <a class="word3" :class="{word4:active==='4'}" @click="handleTabClick('4')">待收货</a>
      <a class="word3" :class="{word4:active==='5'}" @click="handleTabClick('5')">待评价</a>
      <a class="word3" :class="{word4:active==='6'}" @click="handleTabClick('6')">已评价</a>
      <a class="word3" :class="{word4:active==='7'}" @click="handleTabClick('7')">退换货</a>
    </div>

    <!-- 全部订单 -->
    <div class="sofa" v-show="active==='1'" v-for="order in data1" :key="order.id+'t'">
      <div v-for="item in order.goods" :key="item.id">
        <div class="sofa-img">
          <img :src="item.thumb" />
        </div>
        <h6 class="bosc">
          BOSC Xistera
          <span>{{item.name}}</span>
        </h6>
        <p class="green">{{item.sku?item.sku.name:item.color_name}}</p>
        <p class="time" v-show="item.create_time">{{item.create_time}}</p>
        <a href class="pingjia">
          <span>{{item.price | currency}}</span>
        </a>
      </div>

      <a href class="pingjia1">
        <span v-if="order.state==undefined">未付款</span>
        <span v-else-if="order.state=='1'">待发货</span>
        <span v-else-if="order.state=='2'">待收货</span>
        <span v-else-if="order.state=='3'">待评价</span>
        <span v-else-if="order.state=='4'">已评价</span>
        <span v-else-if="order.state=='5'">退换货</span>
        <span v-else-if="order.state=='6'">已退货</span>
      </a>
    </div>
    <!-- 待付款 -->
    <div class="sofa" v-show="active==='2'" v-for="item in data2" :key="item.id+'r'">
      <div class="sofa-img">
        <img :src="item.thumb" />
      </div>
      <h6 class="bosc">
        BOSC Xistera
        <span>{{item.name}}</span>
      </h6>
      <p class="green">{{item.sku?item.sku.name:item.color_name}}</p>
      <p class="time" v-show="item.create_time">{{item.create_time}}</p>
      <a href class="pingjia">
        <span>{{item.price | currency}}</span>
      </a>
      <a href class="pingjia1">
        <span></span>
      </a>
    </div>
    <!-- 待发货 -->
    <div class="sofa" v-show="active==='3'" v-for="order in data3" :key="order.id+'e'">
      <div v-for="item in order.goods" :key="item.id">
        <div class="sofa-img">
          <img :src="item.thumb" />
        </div>
        <h6 class="bosc">
          BOSC Xistera
          <span>{{item.name}}</span>
        </h6>
        <p class="green">{{item.sku?item.sku.name:item.color_name}}</p>
        <p class="time" v-show="item.create_time">{{item.create_time}}</p>
        <a href class="pingjia">
          <span>{{item.price | currency}}</span>
        </a>
      </div>

      <a href class="pingjia1">
        <span>待发货</span>
      </a>
    </div>
    <!-- 待收货 -->
    <div class="sofa" v-show="active==='4'" v-for="order in data4" :key="order.id+'w'">
      <div v-for="item in order.goods" :key="item.id">
        <div class="sofa-img">
          <img :src="item.thumb" />
        </div>
        <h6 class="bosc">
          BOSC Xistera
          <span>{{item.name}}</span>
        </h6>
        <p class="green">{{item.sku?item.sku.name:item.color_name}}</p>
        <p class="time" v-show="item.create_time">{{item.create_time}}</p>
        <a href class="pingjia">
          <span>{{item.price | currency}}</span>
        </a>
      </div>
      <div class="receive">
        <span @click="handleReceive(order.id)">确认收货</span>
        <span @click="handleReject(order.id)">申请退货</span>
      </div>
    </div>
    <!-- 待评价 -->
    <div class="sofa" v-show="active==='5'" v-for="item in data5" :key="item.id+'q'">
      <div class="sofa-img">
        <img :src="item.thumb" />
      </div>
      <h6 class="bosc">
        BOSC Xistera
        <span>{{item.name}}</span>
      </h6>
      <p class="green">{{item.sku?item.sku.name:item.color_name}}</p>
      <p class="time" v-show="item.create_time">{{item.create_time}}</p>
      <a href class="pingjia">
        <span>{{item.price | currency}}</span>
      </a>
      <div class="pingjia1">
        <span @click="$router.push({name:'comment'})">前往评价</span>
      </div>
    </div>
    <!-- 已评价 -->
    <div class="sofa" v-show="active==='6'" v-for="item in data6" :key="item.id+'y'">
      <div class="sofa-img">
        <img :src="item.thumb" />
      </div>
      <h6 class="bosc">
        BOSC Xistera
        <span>{{item.name}}</span>
      </h6>
      <p class="green">{{item.sku?item.sku.name:item.color_name}}</p>
      <p class="time" v-show="item.create_time">{{item.create_time}}</p>
      <a href class="pingjia">
        <span>{{item.price | currency}}</span>
      </a>
      <a href class="pingjia1">
        <span></span>
      </a>
    </div>
    <!-- 退换货 -->
    <div class="sofa" v-show="active==='7'" v-for="order in data7" :key="order.id+'u'">
      <div v-for="item in order.goods" :key="item.id">
        <div class="sofa-img">
          <img :src="item.thumb" />
        </div>
        <h6 class="bosc">
          BOSC Xistera
          <span>{{item.name}}</span>
        </h6>
        <p class="green">{{item.sku?item.sku.name:item.color_name}}</p>
        <p class="time" v-show="item.create_time">{{item.create_time}}</p>
        <a href class="pingjia">
          <span>{{item.price | currency}}</span>
        </a>
      </div>

      <a href class="pingjia1">
        <span></span>
      </a>
    </div>

    <section>
      <goods-list :goods="goods" title="推荐商品"></goods-list>
    </section>
    <main-tab-bar />
  </div>
</template>

<script>
import NavBar from "components/common/navbar/NavBar";
import MainTabBar from "@/components/content/mainTabbar/MainTabBar";
import GoodsList from "components/common/goodsList/GoodsList";
import { mapState } from "vuex";
export default {
  name: "OrderList",
  data() {
    return {
      gid: 0,
      page: 1,
      page1: 1,
      page2: 1,
      page3: 1,
      page4: 1,
      page5: 1,
      page6: 1,
      page7: 1,
      pageSize: 100,
      // 推荐列表
      goods: [],
      active: "1",
      // 购物车数据
      data1: [],
      data2: [],
      data3: [],
      data4: [],
      data5: [],
      data6: [],
      data7: []
    };
  },
  components: {
    NavBar,
    MainTabBar,
    GoodsList
  },
  computed: {
    ...mapState(["cart"])
  },
  created() {},
  mounted() {
    if (this.$route.query.active === "2") {
      this.active = "2";
      this.data2 = this.cart;
    } else if (this.$route.query.active === "3") {
      this.active = "3";
      this.fetchData3();
    } else if (this.$route.query.active === "4") {
      this.active = "4";
      this.fetchData4();
    } else if (this.$route.query.active === "5") {
      this.active = "5";
      this.fetchData5();
    } else if (this.$route.query.active === "7") {
      this.active = "7";
      this.fetchData7();
    } else {
      this.active = "1";
      this.fetchAllData();
    }
    this.fetchGoodsData();
    this.fetchAllData();
  },
  methods: {
    // 确认收货的操作
    async handleReceive(id) {
      let res = await this.$http.put(this.$api.ORDER + "/" + id, { state: 3 });
      if (res.data.code == 200) {
        this.fetchAllData();
        this.fetchData4();
        this.fetchData5();
      }
    },
    // 确认收货的操作
    async handleReject(id) {
      let res = await this.$http.put(this.$api.ORDER + "/" + id, { state: 5 });
      if (res.data.code == 200) {
        this.fetchAllData();
        this.fetchData4();
        this.fetchData7();
      }
    },
    // 获取全部商品
    async fetchAllData() {
      let res = await this.$http.get(this.$api.ORDER, {
        uid: localStorage.getItem("uid"),
        page: this.page1,
        pageSize: this.pageSize
      });
      console.log(res);
      console.log(this.cart);
      this.data1 = res.data.data;
      if (this.cart.length > 0) {
        this.data1.unshift({ id: 0, goods: this.cart });
      }
    },
    async fetchData3() {
      let res = await this.$http.get(this.$api.ORDER, {
        uid: localStorage.getItem("uid"),
        page: this.page2,
        pageSize: this.pageSize,
        state: 1
      });
      this.data3 = [...res.data.data];
    },
    async fetchData4() {
      let res = await this.$http.get(this.$api.ORDER, {
        uid: localStorage.getItem("uid"),
        page: this.page3,
        pageSize: this.pageSize,
        state: 2
      });
      this.data4 = res.data.data;
      console.log(this.data4);
    },
    async fetchData5() {
      let res = await this.$http.get(this.$api.ORDERGOODS, {
        uid: localStorage.getItem("uid"),
        page: this.page5,
        pageSize: this.pageSize,
        state: 3
      });
      this.data5 = [...res.data.data];
    },
    async fetchData6() {
      let res = await this.$http.get(this.$api.ORDERGOODS, {
        uid: localStorage.getItem("uid"),
        page: this.page6,
        pageSize: this.pageSize,
        state: 4
      });
      this.data6 = [...res.data.data];
    },
    async fetchData7() {
      let res = await this.$http.get(this.$api.ORDER, {
        uid: localStorage.getItem("uid"),
        page: this.page7,
        pageSize: this.pageSize,
        state: 5
      });
      this.data7 = [...res.data.data];
    },
    handleTabClick(val) {
      this.active = val;
      if (val === "1" && this.data1.length == 0) {
        this.fetchAllData();
        // 待付款 data2
      } else if (val === "2" && this.data2.length == 0) {
        this.data2 = this.cart;
        // 待发货
      } else if (val === "3" && this.data3.length == 0) {
        this.fetchData3();
      } else if (val === "4" && this.data4.length == 0) {
        this.fetchData4();
      } else if (val === "5" && this.data5.length == 0) {
        this.fetchData5();
      } else if (val === "6" && this.data5.length == 0) {
        this.fetchData6();
      } else if (val === "7" && this.data5.length == 0) {
        this.fetchData7();
      }
    },
    // 获取推荐商品
    async fetchGoodsData() {
      let res = await this.$http.get(this.$api.GOODS, {
        state: 1,
        page: this.page,
        pageSize: this.pageSize,
        recommend: 1
      });
      this.goods = res.data.data;
    }
  }
};
</script>
<style lang='scss' scoped>
@import "styles/indexD";
.header img {
  width: 1.94rem;
  height: 0.4rem;
  margin-top: 0.24rem;
}
.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  background-color: #fff;
}
.receive {
  // display: flex;
  // justify-content: flex-end;

  span {
    display: inline-block;
    margin-top: 1.9rem;
    margin-right: 0.2rem;
    border: 1px solid #c4ab23;
  }
}
</style>
